@import url('https://fonts.googleapis.com/css2?family=Play&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mulish&family=Play&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oxygen&display=swap');
@import "//netdna.bootstrapcdn.com/font-awesome/3.0/css/font-awesome.css";
 *{
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   /* font-family: 'Play', sans-serif; */
   font-family: 'Mulish', sans-serif;
 }
 .navbar{
   background-color: white !important;
 }
 .navbar-brand{
   font-size: 2rem !important;
   color: #565387 !important;
 }
 .navbar a{
   text-transform: capitalize;
   font-size: 1.1rem;
 }
 .navbar a:hover{
   color: #3498db !important;
 }
 .nav-link{
   margin: 0 3px 0 3px !important;
 }
 .menu{
   font-weight: bold;
   border-bottom: 2px solid #00a8ff;
 }

 /* Home page css */
#header{
  width: 100%;
  height: 80vh;
  margin-top: 80px;
}
#header h1{
  margin: 0 0 10px 0;
  font-size: 64px;
  font-weight: 700;
  color: #000;
}
#header h2{
  color: #484848;
  font-size: 24px;
}
#header img{
  text-align: right;
}
 .btn-get-started{
   text-decoration: none;
   color:#3498db ;
   padding: 12px 30px;
   border: 1px solid #3498db;
   border-radius: 50px;
   transition: 0.3s;
 }
 .btn-get-started:hover{
   color: white;
   background-color: #3498db;
   text-decoration: none;
 }
 .paragraph{
  display: inline-block;
  font-size: 23px;
 }
 #header .animated{
   text-align: center;
   animation: up-down 2s ease-in-out infinite alternate-reverse both;
 }
 @-webkit-keyframes up-down{
   0%{
     transform: translatey(10px);
   }
   100%{
     transform: translateY(-10px);
   }
 }
 @keyframes up-down {
   0%{
     transform: translateY(10px);

   }
   100%{
     transform: translateY(-10px);
   }
 }


 /* services */


 .service_heading h1{
  font-family: 'Oxygen', sans-serif;
  color: #565387 ;
 
 }
 .our-service{
   position: relative;
 }
 .service_heading h1::after{
  content: "";
  width: 100%;
  height: 2px;
  color: white;
  background-color: white;
  position: absolute;
  top: -2px;
  left: 0%;
 }
 .card_img{
   width: 21rem  !important;
   height: 240px !important;
 }
 .card{
   margin-bottom: 23px;
 }

 .contact-heading h1{
margin-top: 10px;
  text-transform: capitalize;
  color: #565387;
  display: inline-block;
position: relative;
 }


 
 
 .contact-heading h1::before{
  content: "";
  width: 30%;
  height: 35px;
  background: linear-gradient(#3498db,#fff);
  position: absolute;
  top: -20px;
  left: 0;
  z-index: -1;
  transform: rotate(10deg);
  border-bottom-left-radius: 35px;
  border-top-left-radius: 57px;

left: 0px;
 }

 ::placeholder{
  font-family: FontAwesome;
 }